/**=====================
     Header CSS
==========================**/
.header-style-1 {
    position: relative;
    z-index: 1;

    .top-header {
        background-color: rgba(var(--theme-blue-dark), 1);
        padding-block: 11px;
        @include flex_common ($dis: flex, $align: center, $justify: space-between);
        width: 100%;

        &.blue-color {
            background-color: rgba(var(--theme-blue2), 1);
        }

        .left-header {
            display: flex;
            align-items: center;

            .dropdown-box {
                display: inline-block;
                padding-right: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));
                margin-right: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));
                border-right: 1px solid rgba(var(--white), 0.2);

                >ul {
                    display: flex;
                    align-items: center;
                    gap: calc(9px + (20 - 9) * ((100vw - 320px) / (1920 - 320)));

                    >li {
                        .theme-form-select {
                            padding: 0;
                            background-color: transparent;
                            color: rgba(var(--white), 1);
                            border: none;
                            background-image: none;
                            position: relative;

                            .dropdown-toggle {
                                font-size: 14px;
                                font-weight: 400;
                                color: rgba(var(--white), 1);
                                padding: revert;
                                @include flex_common;
                                padding: 0;
                                line-height: 1;
                                border: none;

                                img {
                                    @include pseudowh($width: 20px, $height: 12px);
                                    margin-right: 8px;
                                    object-fit: cover;

                                    [dir="rtl"] & {
                                        margin-right: unset;
                                        margin-left: 8px;
                                    }
                                }

                                &:active {
                                    border-color: transparent;
                                }

                                &:focus {
                                    box-shadow: none;
                                }

                                &::after {
                                    @include pos($pos: relative, $content: "\ea4e");
                                    @include font;
                                    margin-left: 3px;
                                    right: 0;
                                    color: rgba(var(--white), 1);
                                    border: none;
                                    vertical-align: unset;
                                    font-size: 17px;

                                    [dir="rtl"] & {
                                        margin-left: unset;
                                        margin-right: 8px;
                                    }
                                }
                            }

                            .dropdown-menu {
                                min-width: auto;
                                width: 100%;
                                padding-block: 5px;

                                li {
                                    width: 100%;

                                    +li {
                                        margin-left: 0;

                                        [dir="rtl"] & {
                                            margin-left: unset;
                                            margin-right: 0;
                                        }
                                    }

                                    .dropdown-item {
                                        @include flex_common;
                                        padding-block: 0;
                                        font-size: 14px;
                                        padding-inline: 6px;
                                        padding: 4px 5px;

                                        &:hover,
                                        &:focus {
                                            background-color: transparent;
                                        }

                                        &.active,
                                        &:active {
                                            color: rgba(var(--title-color), 1);
                                            background-color: transparent;
                                        }

                                        img {
                                            width: 20px;
                                            margin-right: 8px;

                                            [dir="rtl"] & {
                                                margin-right: unset;
                                                margin-left: 8px;
                                            }
                                        }

                                        span {
                                            line-height: 1;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .social-box {
                .social-list {
                    display: flex;
                    align-items: center;
                    gap: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));

                    li {
                        &:hover {
                            a {
                                color: rgba(var(--theme-color), 1);

                                &::before {
                                    width: 100%;
                                    background-color: rgba(var(--theme-color), 1);
                                }
                            }
                        }

                        a {
                            position: relative;
                            color: rgba(var(--white), 1);
                            font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
                            transition: color .5s ease-in-out;

                            &::before {
                                @include pos;
                                bottom: 0;
                                left: 0;
                                @include pseudowh($width: 0, $height: 1px);
                                background-color: rgba(var(--white), 1);
                                transition: all .5s ease-in-out;
                            }
                        }
                    }
                }
            }
        }

        .middle-header {
            @media (max-width: 1145px) {
                display: none;
            }

            .middle-content {
                p {
                    font-weight: 400;
                    font-size: 14px;
                    color: rgba(var(--white), 1);
                    overflow: hidden;
                    position: relative;
                    height: 20px;

                    span {
                        display: inline-block;
                        color: rgba(var(--white), 1);
                        position: relative;
                        white-space: nowrap;
                        top: 0;
                        left: 0;
                        animation: move 4s;
                        animation-iteration-count: infinite;
                        animation-delay: 1s;
                    }
                }
            }
        }

        .right-header {
            @media (max-width: 670px) {
                display: none;
            }

            .content-list {
                display: flex;
                align-items: center;
                gap: calc(8px + (20 - 8) * ((100vw - 320px) / (1920 - 320)));

                li {
                    &:hover {
                        a {
                            color: rgba(var(--theme-color), 1);

                            &::before {
                                width: 100%;
                                background-color: rgba(var(--theme-color), 1);
                            }
                        }
                    }

                    a {
                        font-weight: 400;
                        font-size: 14px;
                        color: rgba(var(--white), 1);
                        position: relative;
                        transition: color .5s ease-in-out;

                        &::before {
                            @include pos;
                            bottom: 0;
                            left: 0;
                            @include pseudowh($width: 0, $height: 1px);
                            background-color: rgba(var(--white), 1);
                            transition: all .5s ease-in-out;
                        }
                    }
                }
            }
        }

        &.theme-header {
            background-color: rgba(var(--theme-color3), 1);
        }

        &.theme-header2 {
            background-color: rgba(var(--theme-color4), 1);
        }
    }

    .border-b-color {
        border-bottom: 1px solid rgba(var(--content-color), 0.1);
    }

    .main-header {
        padding-block: 15px;
        background-color: rgba(var(--theme-blue), 1);
        position: relative;
        @include flex_common ($dis: flex, $align: center, $justify: space-between);

        @include mq-max(lg) {
            gap: 25px;
        }

        .left-header {
            display: flex;
            align-items: center;

            .header-logo {
                height: calc(36px + (50 - 36) * ((100vw - 320px) / (1920 - 320)));

                img {
                    @extend %img-contain;
                }
            }
        }

        .middle-header {
            .close-icon {
                right: 10px;
                cursor: pointer;
                display: none;
                position: absolute;
                top: 10px;
                transform: unset;
                color: rgba(var(--content-color), 1);
                z-index: 1;
                font-size: 21px;
                @include pseudowh($width: unset, $height: unset);

                @include mq-max(lg) {
                    display: block;
                }
            }

            .search-form {
                @media (min-width: 992px) {
                    position: relative;
                    inset: unset;
                    display: flex;
                    transform: unset;
                    @include pseudowh($width: max-content, $height: max-content);
                    background-color: transparent;
                    visibility: visible;
                    z-index: 0;
                }

                @include mq-max(lg) {
                    @include pseudowh($width: 100%, $height: calc(40vh + (50 - 40) * ((100vw - 320px) / (991 - 320))));
                    top: 0;
                    left: 0;
                    transform: unset;
                    display: flex;
                    align-items: center;
                    transform: translateY(-50%);
                    opacity: 0;
                    visibility: hidden;
                    position: absolute;
                    padding-inline: calc(15px + (160 - 15) * ((100vw - 320px) / (1920 - 320)));
                    z-index: 1;
                    transition: all 0.5s ease-in-out;
                    background-color: rgba(var(--white), 1);
                    z-index: 2;
                    top: 0;
                    position: fixed;

                    &.show {
                        opacity: 1;
                        visibility: visible;
                        transform: translateY(0);
                    }
                }

                .input-group {
                    @include mq-max(lg) {
                        flex-wrap: nowrap;
                    }

                    .form-control {
                        border: none;
                        padding: 15px;
                        font-weight: 400;
                        font-size: 14px;
                        color: rgba(var(--content-color), 1);
                        width: 780px;
                        z-index: 0;

                        @media (max-width: 1667px) {
                            width: 690px;
                        }

                        @media (max-width: 1550px) {
                            width: 590px;
                        }

                        @media (max-width: 1420px) {
                            width: 450px;
                        }

                        @media (max-width: 1254px) {
                            width: 290px;
                        }

                        @media (max-width: 1061px) {
                            border-radius: 0 6px 6px 0 !important;
                            border: none;
                            margin: 0 !important;
                        }

                        @media (max-width: 992px) {
                            width: 100%;
                            background-color: #F2F3F6;
                            border-radius: 0 5px 5px 0 !important;
                        }
                    }

                    .input-group-text {
                        padding: 0;
                        border: none;
                        background-color: #e6e7e9;
                        border-radius: 5px 0 0 5px !important;

                        .form-select {
                            border: none;
                            text-align: center;
                            font-weight: 400;
                            font-size: 14px;
                            color: rgba(var(--content-color), 1);
                            background-color: transparent;
                            padding: 0 16px 0 0;
                        }
                    }

                    .search-button {
                        background-color: rgba(var(--theme-color), 1);
                        border: 1px solid transparent;
                        padding: 14px 15px;
                        line-height: 1;
                        font-size: 19px;
                        border-radius: 5px;
                        color: rgba(var(--white), 1);
                        z-index: 1;

                        @media (max-width: 1061px) {
                            position: absolute;
                            @include center(vertical);
                            right: 0;
                            border: none;
                            height: 100%;
                        }

                        @include mq-max(lg) {
                            display: none;
                        }

                        &.search-light-color {
                            background-color: rgba(var(--white), 0.2);
                        }
                    }
                }
            }
        }

        .right-header {
            .header-icon {
                display: flex;
                align-items: center;
                gap: 14px;

                @include mq-max(lg) {
                    gap: calc(8px + (14 - 8) * ((100vw - 320px) / (600 - 320)));
                }

                >li {
                    position: relative;

                    @include mq-max(sm) {
                        display: none;
                    }

                    &:first-child,
                    &:last-child {
                        @include mq-max(sm) {
                            display: block;
                        }
                    }

                    &.contact-list {
                        margin-right: 9px;

                        @include mq-max(lg) {
                            margin-right: 0;
                        }
                    }

                    &:first-child {
                        display: none;

                        @include mq-max(lg) {
                            display: block;
                        }
                    }

                    >a {
                        color: rgba(var(--white), 1);
                        display: flex;
                        align-items: center;
                        gap: 12px;
                        position: relative;

                        .label {
                            position: absolute;
                            padding-top: 1px;
                            top: -4px;
                            right: -6px;
                            color: rgba(var(--white), 1);
                            background-color: rgba(var(--theme-color2), 1);
                            @include pseudowh($width: calc(13px + (18 - 13) * ((100vw - 320px) / (1920 - 320))), $height: calc(13px + (18 - 13) * ((100vw - 320px) / (1920 - 320))));
                            border-radius: 100%;
                            @include flex_common;
                            font-size: calc(10px + (13 - 10) * ((100vw - 320px) / (1920 - 320)));
                            font-weight: 500;
                        }

                        i {
                            font-size: 25px;
                            color: rgba(var(--white), 1);

                            @include mq-max(lg) {
                                font-size: calc(17px + (25 - 17) * ((100vw - 320px) / (991 - 320)));
                            }
                        }

                        .iconsax {
                            --size: 25px;
                            --Color: rgba(var(--white), 1);

                            @include mq-max(lg) {
                                --size: calc(17px + (25 - 17) * ((100vw - 320px) / (991 - 320)));
                            }
                        }

                        >div {
                            @include mq-max(lg) {
                                display: none;
                            }
                        }

                        h6,
                        h5 {
                            font-size: 14px;
                        }

                        h6 {
                            margin-bottom: 3px;
                        }
                    }
                }
            }
        }

        &-theme-color {
            background-color: rgba(var(--theme-color3), 1);
        }
    }

