.header-style-2 {
    position: relative;

    .top-header {
        background-color: rgba(var(--theme-color3), 1);
        padding-block: 11px 6px;
        @include flex_common ($dis: flex, $align: center, $justify: space-between);
        width: 100%;
        position: relative;
        z-index: 1;

        &::before {
            @include pos;
            left: 0;
            bottom: -11px;
            background-image: url(../svg/header-shape.svg);
            @include pseudowh($width: 100%, $height: 11px);
            z-index: -1;
        }

        .left-header {
            display: flex;
            align-items: center;

            .dropdown-box {
                display: inline-block;
                padding-right: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));
                margin-right: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));
                border-right: 1px solid rgba(var(--white), 0.2);

                >ul {
                    display: flex;
                    align-items: center;
                    gap: calc(9px + (20 - 9) * ((100vw - 320px) / (1920 - 320)));

                    >li {
                        .theme-form-select {
                            padding: 0;
                            background-color: transparent;
                            color: rgba(var(--white), 1);
                            border: none;
                            background-image: none;
                            position: relative;

                            .dropdown-toggle {
                                font-size: 14px;
                                font-weight: 400;
                                color: rgba(var(--white), 1);
                                padding: revert;
                                @include flex_common;
                                padding: 0;
                                line-height: 1;
                                border: none;

                                img {
                                    @include pseudowh($width: 20px, $height: 12px);
                                    margin-right: 8px;
                                    object-fit: cover;

                                    [dir="rtl"] & {
                                        margin-right: unset;
                                        margin-left: 8px;
                                    }
                                }

                                &:active {
                                    border-color: transparent;
                                }

                                &:focus {
                                    box-shadow: none;
                                }

                                &::after {
                                    @include pos($pos: relative, $content: "\ea4e");
                                    @include font;
                                    margin-left: 3px;
                                    right: 0;
                                    color: rgba(var(--white), 1);
                                    border: none;
                                    vertical-align: unset;
                                    font-size: 17px;

                                    [dir="rtl"] & {
                                        margin-left: unset;
                                        margin-right: 8px;
                                    }
                                }
                            }

                            .dropdown-menu {
                                min-width: auto;
                                width: 100%;
                                padding-block: 5px;

                                li {
                                    width: 100%;

                                    +li {
                                        margin-left: 0;

                                        [dir="rtl"] & {
                                            margin-left: unset;
                                            margin-right: 0;
                                        }
                                    }

                                    .dropdown-item {
                                        @include flex_common;
                                        padding-block: 0;
                                        font-size: 14px;
                                        padding-inline: 6px;
                                        padding: 4px 5px;

                                        &:hover,
                                        &:focus {
                                            background-color: transparent;
                                        }

                                        &.active,
                                        &:active {
                                            color: rgba(var(--title-color), 1);
                                            background-color: transparent;
                                        }

                                        img {
                                            width: 20px;
                                            margin-right: 8px;

                                            [dir="rtl"] & {
                                                margin-right: unset;
                                                margin-left: 8px;
                                            }
                                        }

                                        span {
                                            line-height: 1;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .social-box {
                .social-list {
                    display: flex;
                    align-items: center;
                    gap: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));

                    li {
                        &:hover {
                            a {
                                color: rgba(var(--theme-color), 1);

                                &::before {
                                    width: 100%;
                                    background-color: rgba(var(--theme-color), 1);
                                }
                            }
                        }

                        a {
                            position: relative;
                            color: rgba(var(--white), 1);
                            font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
