/**=====================
     Banner CSS
==========================**/
.left-banner-content {
    padding: calc(10px + (20 - 10) * ((100vw - 1200px) / (1920 - 1200))) calc(10px + (25 - 10) * ((100vw - 1200px) / (1920 - 1200)));
    background-color: rgba(var(--white), 1);
    @include flex_common ($dis: flex, $align: center, $justify: space-between);

    .accessories-computer-box {
        width: 100%;

        h4 {
            font-size: calc(15px + (18 - 15) * ((100vw - 1200px) / (1920 - 1200)));
            margin-bottom: 13px;
            @include line;
        }

        .deal-list {
            li {
                display: block;
                width: 100%;

                +li {
                    margin-top: 6px;
                }

                a {
                    font-size: calc(14px + (16 - 14) * ((100vw - 1200px) / (1920 - 1200)));
                    color: rgba(var(--content-color), 1);
                    text-transform: capitalize;
                }

                &:last-child {
                    a {
                        color: rgba(var(--theme-green), 1);
                    }
                }
            }
        }
    }
}

.banner-box-9 {
    height: 150px;
}

.banner-row {
    .swiper {
        .swiper-wrapper {
            .swiper-slide {
                &:nth-child(2n) {
                    .right-image-box {
                        img {
                            order: 1;
                        }
                    }
                }

                &:nth-child(1) {
                    .banner-content-1 {
                        background-image: url("../images/banner/banner-27.jpg");
                    }
                }

                &:nth-child(2) {
                    .banner-content-1 {
                        background-image: url("../images/banner/banner-28.jpg");
                    }
                }

                &:nth-child(3) {
                    .banner-content-1 {
                        background-image: url("../images/banner/banner-29.jpg");
                    }
                }

                &:nth-child(4) {
                    .banner-content-1 {
                        background-image: url("../images/banner/banner-30.jpg");
                    }
                }
            }
        }
    }
}

.banner-content-1 {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: calc(13px + (23 - 13) * ((100vw - 320px) / (1920 - 320))) calc(13px + (20 - 13) * ((100vw - 320px) / (1920 - 320)));
    position: relative;
    height: 100%;

    .banner-title {
        margin-top: -2px;
        color: rgba(var(--content-color), 1);
        margin-bottom: 4px;
    }

    h3 {
        font-size: calc(19px + (24 - 19) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 600;
        @include line;
    }

    h2 {
        font-size: 16px;
        font-weight: 400;
        color: rgba(var(--title-color), 1);
        text-transform: uppercase;
        margin-top: 6px;
        @include line;

        &.name {
            text-transform: none;
            font-family: $dancing-script;
            font-size: calc(29px + (40 - 29) * ((100vw - 320px) / (1920 - 320)));
            color: rgba(var(--theme-color), 1);
            margin-top: calc(-9px + (-15 - -9) * ((100vw - 320px) / (1920 - 320)));
            font-weight: bold;
            line-height: normal;
        }
    }

    .image-content {
        @include flex_common ($dis: flex, $align: center, $justify: space-between);
        margin-block: 18px 25px;

        @include mq-max(lg) {
            gap: calc(12px + (50 - 12) * ((100vw - 576px) / (992 - 576)));
        }

        @include mq-max(sm) {
            gap: calc(20px + (50 - 20) * ((100vw - 320px) / (576 - 320)));
        }

        .left-image-box {
            @include pseudowh($width: calc(128px + (158 - 128) * ((100vw - 1400px) / (1920 - 1400))), $height: calc(128px + (158 - 128) * ((100vw - 1400px) / (1920 - 1400))));

            @media (max-width: 1400px) {
                @include pseudowh($width: calc(128px + (140 - 128) * ((100vw - 992px) / (1400 - 992))), $height: calc(128px + (140 - 128) * ((100vw - 992px) / (1400 - 992))));
            }

            @include mq-max(lg) {
                @include pseudowh($width: calc(112px + (150 - 112) * ((100vw - 576px) / (992 - 576))), $height: auto);
            }

            @include mq-max(sm) {
                width: calc(140px + (200 - 140) * ((100vw - 320px) / (576 - 320)));
            }

            .left-image {
                @extend %img-contain;
            }
        }

        .right-image-box {
            width: calc(100% - calc(128px + (158 - 128) * ((100vw - 1400px) / (1920 - 1400))) - calc(8px + (39 - 8) * ((100vw - 1400px) / (1920 - 1400))));
            @include flex_wrap ($dis: flex, $wrap: wrap, $gap: 8px);
            align-items: center;

            @media (max-width: 1400px) {
                width: calc(100% - calc(128px + (140 - 128) * ((100vw - 992px) / (1400 - 992))) - calc(8px + (15 - 8) * ((100vw - 992px) / (1400 - 992))));
            }

            @include mq-max(lg) {
                width: calc(100% - calc(112px + (150 - 112) * ((100vw - 576px) / (992 - 576))) - calc(12px + (50 - 12) * ((100vw - 576px) / (992 - 576))));
            }

            @include mq-max(sm) {
                width: calc(100% - calc(140px + (200 - 140) * ((100vw - 320px) / (576 - 320))) - calc(20px + (50 - 20) * ((100vw - 320px) / (576 - 320))));
            }

            .right-image {
                border-radius: 5px;
            }

            h6 {
                font-weight: 500;
                text-transform: uppercase;
                color: rgba(var(--title-color), 1);
                margin-bottom: 8px;
            }
        }
    }

    p {
        font-weight: 400;
        font-size: 14px;
        margin-bottom: calc(5px + (10 - 5) * ((100vw - 320px) / (1920 - 320)));
        text-transform: capitalize;
        color: rgba(var(--content-color), 1);
        @include line($line: 2);
    }

    h4 {
        font-weight: 500;
        font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
        color: rgba(var(--theme-green), 1);
        display: flex;
        align-items: center;
        gap: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320)));
        margin-bottom: -6px;

        del {
            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
            color: rgba(var(--content-color), 1);
        }
    }

    &.banner-content-2 {
        .image-content {
            .left-image-box {
                width: 204px;
                margin-left: -20px;
            }
        }
    }
}

.banner-box-17 {
    .banner-image {
        mix-blend-mode: darken;
        @include pseudowh($width: 100%, $height: 371px);
        object-fit: cover;
        object-position: bottom;
    }

    .banner-contain {
        padding: calc(13px + (22 - 13) * ((100vw - 1200px) / (1920 - 1200))) calc(16px + (25 - 16) * ((100vw - 1200px) / (1920 - 1200)));
        text-align: center;

        h3 {
            font-size: calc(22px + (30 - 22) * ((100vw - 1200px) / (1920 - 1200)));
            color: rgba(var(--theme-color), 1);
            font-family: $dancing-script;
            position: relative;
            display: inline-block;
            margin-bottom: calc(6px + (10 - 6) * ((100vw - 1200px) / (1920 - 1200)));

            &::before,
            &::after {
                @include pos;
                top: 50%;
                @include pseudowh($width: 60px, $height: 2px);
                border-radius: 100%;
            }

            &::before {
                left: -50px;
                background: linear-gradient(90deg, rgba(var(--theme-color), 1) 12.3%, rgba(var(--theme-color), 0) 72.13%);
            }

            &::after {
                right: -50px;
                background: linear-gradient(-90deg, rgba(var(--theme-color), 1) 12.3%, rgba(var(--theme-color), 0) 72.13%);
            }
        }

        h4 {
            font-size: calc(15px + (20 - 15) * ((100vw - 1200px) / (1920 - 1200)));
            font-weight: 600;
            text-transform: uppercase;
            margin-bottom: calc(4px + (8 - 4) * ((100vw - 1200px) / (1920 - 1200)));

            span {
                color: rgba(var(--theme-color3), 1);
            }
        }

        h5 {
            font-size: calc(15px + (20 - 15) * ((100vw - 1200px) / (1920 - 1200)));
            font-weight: 600;
            color: rgba(var(--theme-color2), 1);

            del {
                color: rgba(var(--content-color), 1);
            }
        }

        p {
            margin-top: 5px;
            margin-bottom: 10px;
            font-size: calc(14px + (16 - 14) * ((100vw - 1200px) / (1920 - 1200)));
            text-transform: capitalize;
            color: rgba(var(--content-color), 0.8);
        }

        .banner-timer {
            @include flex_common;
            gap: calc(3px + (9 - 3) * ((100vw - 1200px) / (1920 - 1200)));

            li {
                background-color: rgba(var(--white), 1);
                padding: calc(9px + (12 - 9) * ((100vw - 1200px) / (1920 - 1200)));
                @include flex_common;
                text-align: center;
                box-shadow: 2.65556px 4.42593px 8.85185px rgba(45, 126, 87, 0.05);
                border-radius: 5px;
                @include pseudowh($width: 54px, $height: 72px);

                >div {
                    line-height: 1;

                    .counter {
                        font-size: calc(18px + (23 - 18) * ((100vw - 1200px) / (1920 - 1200)));
                        font-weight: 600;
                        color: rgba(var(--theme-color3), 1);
                        line-height: 1;
                        position: relative;
                        margin-bottom: 5px;

                        &::before {
                            @include pos;
                            bottom: -2px;
                            @include center(horizontal);
                            background-color: rgba(var(--theme-color), 1);
                            @include pseudowh($width: 80%, $height: 2px);
                        }
                    }

                    span {
                        color: rgba(var(--content-color), 0.8);
                        font-size: 14px;
                        text-transform: capitalize;
                    }
                }
            }
        }

        .banner-btn {
            background-color: rgba(var(--theme-color), 1);
            margin-top: 15px;
            width: 100%;
            @include flex_common;
            gap: 10px;
            font-size: 14px;

            i {
                margin-top: -3px;
                line-height: 1;
            }
        }
    }
}

.banner-box-25 {
    position: relative;
    height: 100%;

    .banner-content {
        padding: 25px 20px;
        position: absolute;
        top: 0;
        left: 0;
        @include pseudowh;
        display: flex;
        align-items: center;

        h4 {
            font-size: calc(19px + (25 - 19) * ((100vw - 1200px) / (1920 - 1200)));
            margin-bottom: calc(6px + (12 - 6) * ((100vw - 1200px) / (1920 - 1200)));
        }

        .banner-product-list {
            display: flex;
            flex-wrap: wrap;
            gap: calc(6px + (12 - 6) * ((100vw - 1200px) / (1920 - 1200)));

            li {
                width: 100%;

                a {
                    font-size: calc(15px + (16 - 15) * ((100vw - 1200px) / (1920 - 1200)));
                    text-transform: capitalize;
                    color: rgba(var(--content-color), 1);
                }

                &:last-child {
                    a {
                        color: rgba(var(--light-blue), 1);
                    }
                }
            }
        }
    }
}