/**=====================
     Category CSS
==========================**/
.category-box {
    text-align: center;

    &:hover {
        h5 {
            color: rgba(var(--title-color), 1);
        }
    }

    img {
        @include pseudowh($width: 100%, $height: 126px);
        object-fit: contain;
    }

    h5 {
        @include line;
        margin-top: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
        color: rgba(var(--content-color), 1);
        transition: 0.3s;
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    }
}

.category-bg-color {
    background-color: rgba(var(--white), 1);
    height: 100%;
    overflow: hidden;

    >div {
        z-index: 0;
        padding: calc(9px + (20 - 9) * ((100vw - 320px) / (1920 - 320))) calc(12px + (25 - 12) * ((100vw - 320px) / (1920 - 320)));
    }

    .category-box-2 {
        text-align: center;

        &:hover {
            .category-image {
                img {
                    transform: scale(1.07);
                }
            }

            h6 {
                color: rgba(var(--title-color), 1);
            }
        }

        .category-image {
            background-color: #F2F3F8;
            @include pseudowh($width: calc(92px + (100 - 92) * ((100vw - 320px) / (1920 - 320))), $height: calc(92px + (100 - 92) * ((100vw - 320px) / (1920 - 320))));
            border-radius: 100%;
            margin-inline: auto;
            overflow: hidden;

            img {
                transition: all 0.3s ease-in-out;
            }
        }

        h6 {
            @include line;
            margin-top: calc(8px + (15 - 8) * ((100vw - 320px) / (1920 - 320)));
            font-weight: 500;
            color: rgba(var(--content-color), 1);
            font-size: 14px;
            transition: all 0.3s ease-in-out;
        }

        &.category-menu {
            .category-image {
                background-color: rgb(254, 229, 1);

                i {
                    font-size: 30px;
                    display: grid;
                    place-items: center;
                    height: 100%;
                }
            }
        }
    }
}

.product-category {
    .category-content {
        text-align: center;
        margin-top: calc(11px + (20 - 11) * ((100vw - 320px) / (1920 - 320)));
        @include flex_wrap ($dis: flex, $wrap: wrap, $gap: calc(2px + (5 - 2) * ((100vw - 320px) / (1920 - 320))));

        h5 {
            font-weight: 500;
            line-height: 1.3;
            width: 100%;
            text-transform: capitalize;
            @include line;

            &.name {
                color: rgba(var(--title-color), 1);
            }

            &.offer {
                color: rgba(var(--theme-green), 1);
            }

            &.rate {
                color: rgba(var(--content-color), 1);
            }
        }
    }
}

.category-box-2 {
    .category-image {
        border-radius: 5px;
    }

    .category-content {
        color: rgba(var(--title-color), 1);
        margin-inline: auto;
        width: calc(100% - 11px);
        background-color: rgba(var(--white), 1);
        border-radius: 100px;
        margin-top: -11px;
        text-align: center;
        padding: 10px;
        text-transform: capitalize;
    }
}

.category-box-3 {
    text-align: center;

    .category-image {
        @include pseudowh($width: 100%, $height: 105px);

        img {
            @extend %img-contain;
        }
    }
}

.category-box-4 {
    text-align: center;
    color: rgba(var(--title-color), 1);
    padding: calc(14px + (28 - 14) * ((100vw - 320px) / (1920 - 320))) calc(10px + (23 - 10) * ((100vw - 320px) / (1920 - 320)));
    background-color: rgba(var(--base-gray), 1);
    box-shadow: 0px 2px 0px rgba(82, 82, 108, 0.15);

    img {
        @include pseudowh($width: 100%, $height: calc(91px + (126 - 91) * ((100vw - 320px) / (1920 - 320))));
        object-fit: contain;
        mix-blend-mode: darken;
    }

    .category-content {
        margin-top: calc(8px + (17 - 8) * ((100vw - 320px) / (1920 - 320)));

        h4 {
            font-size: 16px;
            @include line;
        }

        h6 {
            color: rgba(var(--content-color), 0.8);
            margin-top: calc(0px + (2 - 0) * ((100vw - 320px) / (1920 - 320)));
        }
    }
}

.top-category-section {
    margin-block: 10px;
    z-index: 0;
    position: relative;

    .top-category-box {
        padding: 10px;
        text-align: center;
        background-color: rgba(var(--white), 1);
        text-transform: uppercase;
        color: rgba(var(--title-color), 1);
    }
}

.category-sm-box {
    text-align: center;

    .category-image {
        @include pseudowh($width: 100%, $height: 111px);

        img {
            @extend %img-contain;
        }
    }

    h6 {
        margin-top: 5px;
        text-transform: capitalize;
        color: rgba(var(--content-color), 1);
        @include line;
    }
}

.arrivals-box {
    .swiper-wrapper {
        .swiper-slide {
            &:nth-child(2) {
                .new-arrivals-box {
                    background-color: #EFF8FF;
                }
            }

            &:nth-child(3) {
                .new-arrivals-box {
                    background-color: #F1FCE8;
                }
            }

            &:nth-child(4) {
                .new-arrivals-box {
                    background-color: #EAFAFA;
                }
            }

            &:nth-child(5) {
                .new-arrivals-box {
                    background-color: #FFF3E8;
                }
            }

            &:nth-child(6) {
                .new-arrivals-box {
                    background-color: #E2F8E8;
                }
            }

            &:nth-child(7) {
                .new-arrivals-box {
                    background-color: #E5F5FF;
                }
            }
        }
    }

    .new-arrivals-box {
        padding: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320))) calc(9px + (33 - 9) * ((100vw - 320px) / (1920 - 320)));
        border-radius: 10px;
        background-color: #FEF2F6;
        @include pseudowh;
        text-align: center;

        .arrivals-image {
            @include pseudowh($width: calc(87px + (111 - 87) * ((100vw - 320px) / (1920 - 320))), $height: calc(87px + (111 - 87) * ((100vw - 320px) / (1920 - 320))));
            border-radius: 100%;
            margin: 0 auto calc(11px + (22 - 11) * ((100vw - 320px) / (1920 - 320)));

            img {
                @extend %img-contain;
            }
        }

        h4 {
            font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
            color: rgba(var(--title-color), 1);
        }
    }
}